* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
@media (orientation: portrait) {
  html {
    font-size: 1.7vw;
  }
  html.players_7 main {
    font-size: 1.2vw;
  }
  html.players_10 main {
    font-size: 1vw;
  }
}
@media (orientation: landscape) {
  html {
    font-size: 2vh;
  }
  html.players_7 main {
    font-size: 1.5vh;
  }
  html.players_10 main {
    font-size: 1.3vh;
  }
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}



